@media(max-width:1000px),

(max-height:500px) {

.text{
    margin-bottom: .8vh;
}

    h2 {
        white-space: nowrap;
        font-size: 12px;
        display: inline;
        letter-spacing: 5px;
    }
    #divideLine {
        color:#747474;
        font-family: jubilat-thin,serif;
        font-weight: 200;
        font-style: normal;
    }
   
    body {
        margin-left: 10px;
        margin-bottom: 2px;
    }
    #footer {
        bottom: 0px;
        width: 100%;
        position: relative;
        text-align: center;
        text-align: center;
        font-size: 13px;
    }
}

@media (min-width: 500px)
{
    h1 {
        white-space: nowrap;
    }

    body{  overflow-x: scroll;}
  
